body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f5f5f5;
  background: linear-gradient(#e994be,#0bbef5);
  font-family: 'Courier New', Courier, monospace;
}

.board-row:after {
  clear: both;
  display: table;
}

.status {
  margin-bottom: 18px;
 
}

.square {
  background: #fadddd;
  border: 2px solid #c97c7c; /* Cor da borda */
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 50px; /* Ajuste de acordo com a altura do quadrado */
  height: 50px; /* Altura do quadrado */
  margin-right: -2px;
  margin-top: -2px;
  padding: 0;
  text-align: center;
  width: 50px; /* Largura do quadrado */
  color: #000000; /* Cor do texto */
}

.square {
  width: 50px;
  height: 50px;
  border: 2px solid #ff00b3;
  outline: none;
}


.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}
